import React from "react";
import "../Style/Components/Header.css"
import NavBar from "./NavBar";
import Logo from "./Logo";
import $ from 'jquery';

class Header extends React.Component{

    constructor(props){
        super(props);
    }

    componentDidMount() {
        let currentPosition = $(window).scrollTop();
        let scrollIndex = 0 ;
        let date = new Date();
        let lastTimesSec = date.getMilliseconds();
        let sectionHeight  = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
        window.onscroll = function() {
            let nowSec = date.getMilliseconds();
            /*
            if(parseInt(nowSec) > parseInt(lastTimesSec) ) {
                alert("yes")
                if (currentPosition > $(window).scrollTop()) {
                    scrollIndex = scrollIndex - 1;
                    $(window).scrollTop(sectionHeight * scrollIndex);
                } else {

                    scrollIndex = scrollIndex + 1;
                    $(window).scrollTop(sectionHeight * scrollIndex);
                }

                currentPosition = $(window).scrollTop();

            }else{

            }

            lastTimesSec = nowSec;
             */
            if($(window).scrollTop() > sectionHeight/2) {
                $( ".HeaderLogoContainer" ).css("display", "block")
                $( ".Header" ).css("display", "block")
                $( ".Header" ).css("background", "-webkit-linear-gradient(white, white)")
                $( ".navA" ).css("color", "#0ba4d4")
                $( ".navA" ).css("border-color", "#0ba4d4")
                $( "#logoBlue" ).css("display", "block")
                $( "#logoWHite" ).css("display", "none")
                $( "#navApply" ).css("background-color", "#0ba4d4")
                $( "#navApply" ).css("border-radius", "20px")
                $( ".navApplyText" ).css("color", "white")
                var isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
                if(isMobile){
                    $( ".NavBar" ).css("top", "10%")
                }
            } else {
                $( ".HeaderLogoContainer" ).css("display", "none")
                $( ".navA" ).css("color", "white")
                $( ".navA" ).css("border-color", "")
                $( ".Header" ).css("background", "")
                $( ".Header" ).css("background-color", "")
                $( "#logoBlue" ).css("display", "none")
                $( "#logoWHite" ).css("display", "block")

                //Apply button
                $( "#navApply" ).css("background-color", "")
                $( "#navApply" ).css("border-radius", "20px")
                $( ".navApplyText" ).css("color", "white")

                //NavBar
                var isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
                if(isMobile){
                    $( ".navA" ).css("color", "#0ba4d4")
                    $( ".NavBar" ).css("top", "0%")
                }

            }

            currentPosition = sectionHeight;
        };
    }
    render(){
        return(
            <React.Fragment>
                <div className={"Header"}>
                    <div className="HeaderLogoContainer">
                        <Logo/>
                    </div>
                    <NavBar/>
                </div>
            </React.Fragment>

        )
    }
}


export default Header